/*--------------------header----------------------*/
@import "../_source/mob_header";
.header{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: #fff;
    z-index: 900;

    &__wrapper{
    	height: 58px;
    	display: flex;
    	align-items: center;
    	justify-content: space-between;
    }
    &__logo{
    	width: 118px;
    	img{
    		display: block;
    	}
    }
}
.header-nav{
	display: none;
}
/*--------------------END header----------------------*/ 